@import "/www/css/service";

.client_skirmish_map_item {
    @extend %ui-header-text;

    align-items: center;
    background-color: $dark-blue;
    display: flex;
    flex-direction: row;
    font-size: 20px;
    gap: 0 8px;
    justify-content: space-between;
    padding: 12px 8px;
}

.client_skirmish_map_item__left,
.client_skirmish_map_item__right {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
}

.client_skirmish_map_item__left {
    overflow: hidden;
    padding-left: 4px;
}

.client_skirmish_map_item__right {
    justify-content: flex-end;
}

.client_skirmish_map_item__player_count {
    display: block;
    flex-shrink: 0;
}

.client_skirmish_map_item__map_name {
    @extend %ellipsis;
    @extend %ui-header-text;

    display: block;
}

.client_skirmish_map_item__show_map {
    @extend %ui-header-text;

    background-color: transparent;
    border: 0;
    display: block;
    margin: 0;
    padding: 0;
}

