@import '/www/css/service';
@import '/www/css/var';

.game_snackbar__content {
    @extend %ui-border-1;

    align-items: center;
    animation-timing-function: ease-in-out;
    background-origin: border-box;
    bottom: calc($bottom-status-bar-size / 2);
    box-shadow: 4px 4px 0 0 #000a;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    gap: 0 16px;
    justify-content: space-between;
    left: 50%;
    max-height: 80vh;
    max-width: 80vw;
    opacity: 0;
    padding: 4px 8px;
    position: fixed;
    transform: translate3d(-50%, 200%, 0);
    z-index: $mame-snackbar-overlay-z-index;

    &.game_snackbar__content__error {
        background-color: #c00;
    }
}

.game_snackbar__children {
    @extend %ui-header-text;

    display: block;
    font-size: 16px;
}

.game_snackbar__progress_bar {
    background-color: #fff;
    bottom: -6px;
    height: 3px;
    left: 0;
    position: absolute;
    width: 100%;
}
