.frame_by_frame {
    overflow: hidden;
}

.frame_by_frame_list {
    animation-direction: normal;
    animation-duration: 4s;
    animation-fill-mode: backwards;
    animation-iteration-count: infinite;
    display: block;
}

.frame_by_frame_image {
    display: block;
    image-rendering: pixelated;
}
