@import "/www/css/service";
@import "/www/css/var";

.client_square_info {
    box-sizing: border-box;
    display: block;
    flex-grow: 0;
    flex-shrink: 0;
    height: $bottom-status-bar-size;
    image-rendering: pixelated;
    position: relative;
    width: $bottom-status-bar-size;
    z-index: 1;
}

.client_square_info__border,
.client_square_info__image {
    display: block;
    height: $bottom-status-bar-size;
    left: 0;
    position: absolute;
    top: 0;
    width: $bottom-status-bar-size;
}

.client_square_info__border {
    @extend %ui-border-1;

    background-color: transparent;
    box-sizing: border-box;
    z-index: 1;
}

.client_square_info__image {
    object-fit: cover;
    object-position: bottom;
    z-index: 0;
}

.client_square_info__armor_text {
    bottom: calc($square-size / 4);
    justify-content: center;
    left: 0;
    position: absolute;
    width: 100%;
    z-index: 0;
}
