@import '/www/css/service';
@import '/www/css/var';

.game_popup__overlay {
    backdrop-filter: blur(1.5px) grayscale(0.5);
    background-color: #0005;
    height: 100%;
    left: 0;
    opacity: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: $mame-popup-overlay-z-index;
}

.game_popup__content {
    @extend %ui-border-1;

    align-items: stretch;
    animation-timing-function: ease-in-out;
    background-origin: border-box;
    box-shadow: 4px 4px 0 0 #000a;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    left: 50%;
    max-height: 80vh;
    max-width: 80vw;
    opacity: 0;
    position: fixed;
    top: 50%;
    transform: translate3d(-50%, -50%, 0) scale(2);
    width: 480px;
    z-index: $mame-popup-overlay-z-index;
}

.game_popup__button_as_header,
.game_popup__header {
    @extend %ui-header-text;

    background-color: transparent;
    border: 0;
    display: block;
    font-size: 24px;
    margin: 0;
    padding: 16px 0;
    text-align: center;
    width: 100%;
}

.game_popup__button_as_header {
    cursor: pointer;
}

.game_popup__text {
    @extend %ui-header-text;

    display: block;
    font-size: 18px;
    margin: 0;
    text-align: center;
}

.game_popup__footer {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 16px 0 8px;
}

.game_popup__story_markdown_list {
    @extend %scrollable;

    display: block;
    padding: 0 16px;
}

.game_popup__story_markdown_item {
    display: block;
}
