@import './../client-game-view-container/client-game-view-container';

.effect_layer {
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.effect_layer__attack_square {
    align-items: center;
    display: flex;
    height: $square-size;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: $square-size;
}

.effect_layer__occupy_image {
    display: block;
    height: $square-size;
    image-rendering: pixelated;
    left: 0;
    position: absolute;
    top: 0;
    transform-origin: left top;
    width: $square-size;
}
