@import "/www/css/var";

.map_editor__click_hunter_layer {
    align-items: flex-start;
    background-image: linear-gradient(0deg, #888 0%, #888 2%, transparent 2%, transparent 98%, #888 98%, #888 100%),
        linear-gradient(90deg, #888 0%, #888 2%, transparent 2%, transparent 98%, #888 98%, #888 100%);
    background-size: $square-size $square-size;
    display: flex;
    flex-flow: row wrap;
    height: 100%;
    justify-content: flex-start;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.map_editor__click_hunter_layer__cell {
    background-color: transparent;
    border: 0;
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    height: $square-size;
    width: $square-size;
}
