@import '/www/css/var';
@import '/www/css/service';

.page {
    align-items: stretch;
    background-color: #333C57;
    background-image: linear-gradient(0deg, #333C57, #7F8598);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    color: $main-font-color;
    display: flex;
    flex-direction: column;
    font-family: $main-font-family-monospace;
    height: 100%;
    justify-content: flex-start;
    line-height: 1.4;
    margin: 0;
    position: fixed;
    width: 100%;
}
