@import "/www/css/var";
@import "/www/css/service";

.map_editor_draw_panel {
    align-items: stretch;
    background-color: #7d7e99;
    border-right: 1px solid #ccc;
    bottom: $bottom-status-bar-size;
    display: flex;
    flex-direction: column;
    gap: 0 8px;
    justify-content: flex-start;
    left: 0;
    position: fixed;
    top: 0;
    width: $map-editor-draw-panel-size;
    z-index: $game-ui-z-index;
}

.map_editor_draw_panel__tab_button_list {
    align-items: center;
    border-bottom: 2px solid #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 4px 0;
}

.map_editor_draw_panel__tab_content {
    @extend %scrollable;

    align-content: start;
    align-items: flex-start;
    display: flex;
    flex-flow: row wrap;
    gap: 8px 0;
    justify-content: space-evenly;
    padding: 4px 0;

    &.map_editor_draw_panel__tab_content__zero_shrink {
        flex-shrink: 0;
    }

    &.map_editor_draw_panel__tab_content__two_columns {
        padding-left: 12px;
        padding-right: 12px;
    }
}

.map_editor_draw_panel__map_size {
    @extend %ui-header-text;

    display: block;
    margin: 0;
    padding: 0;
    padding: 4px 0 0;
    text-align: center;
}

.map_editor_draw_panel__map_name__label {
    box-sizing: border-box;
    display: block;
    flex-shrink: 0;
    max-width: 100%;
    padding: 4px;
}

.map_editor_draw_panel__map_name__input {
    box-sizing: border-box;
    display: block;
    font-size: 14px;
    max-width: 100%;
    padding: 4px;
}

.map_editor_draw_panel__map_name__input_name {
    box-sizing: border-box;
    display: block;
    margin: 0;
    max-width: 100%;
    padding: 0 4px;
    text-align: center;
}
