@import './../client-game-view-container/client-game-view-container';

.building_effect_layer {
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

%farm-smoke-animation-settings {
    animation-direction: normal;
    animation-duration: 2.5s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: steps(5);

}

.building_effect_item_farm_smoke {
    @extend %farm-smoke-animation-settings;

    animation-name: smoke-move-up;
    display: block;
    height: $square-size;
    overflow: hidden;
    position: absolute;
    width: $square-size;
}

.building_effect_item_farm_smoke_image {
    @extend %farm-smoke-animation-settings;

    animation-name: smoke-scroll-image;
    display: block;
    height: $square-size * 5;
    image-rendering: pixelated;
    width: $square-size;
}

@keyframes smoke-move-up {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-75%);
    }
}

@keyframes smoke-scroll-image {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0%);
    }
}
