@import '/www/css/var';
@import '/www/css/service';

.client_game_view_container {
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    //height: calc(100vh - $bottom-status-bar-size);
    justify-content: center;
    overflow: hidden;
    touch-action: none;
    width: 100%;
}

.child_wrapper {
    @extend %ui-border-game-map;

    display: block;
    position: relative;
    transform-origin: left top;
    z-index: $game-container-z-index;
}

.client_game_view_container__animated_div {
    touch-action: none;
}
