@import './../../client-game-view-container/client-game-view-container';

%click-hunter-cell,
.click_hunter_cell {
    background-clip: content-box;
    background-color: transparent;
    border: 0;
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    flex-shrink: 0;
    font-size: 8px;
    height: $square-size;
    image-rendering: pixelated;
    margin: 0;
    padding: 0;
    width: $square-size;
}

.click_hunter_empty_cell {
    @extend %click-hunter-cell;

    /*
    &:hover {
        background-color: #fff5;
    }
    */
}

@keyframes click-hunter-cell-image-show {
    from {
        transform: scale(0);
    }

    to {
        transform: scale(1);
    }
}

%click-hunter-cell-image,
.click_hunter_cell__image {
    animation-direction: normal;
    animation-duration: 0.35s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
    animation-name: click-hunter-cell-image-show;
    display: block;
    height: $square-size;
    width: $square-size;
}

.client_game_click_hunter_cell_select {
    @extend %click-hunter-cell;
}

.client_game_click_hunter_cell_select_open_button {
    @extend %click-hunter-cell;
}

.client_game_click_hunter_cell_select_open_button__image {
    @extend %click-hunter-cell-image;
}

@keyframes show-list-animation {
    from {
        transform: scale(0);
    }

    to {
        transform: scale(1);
    }
}

.client_game_click_hunter_cell_select_button_list {
    animation-direction: normal;
    animation-duration: 0.35s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
    animation-name: show-list-animation;
    background-color: #fff;
    border: 0;
    box-shadow: -2px 2px #000;
    display: flex;
    flex-flow: row nowrap;
    height: $square-size;
    margin: 0;
    outline: 1px solid #888;
    padding: 0;
    position: fixed;
    z-index: 1;
}
