@import '/www/css/var';

.client_unit_stats {
    align-content: center;
    background-color: #0008;
    bottom: $bottom-status-bar-size;
    display: flex;
    flex-direction: row;
    image-rendering: pixelated;
    justify-content: center;
    padding: 4px 4px 3px;
    position: fixed;
    right: 0;
    z-index: $game-ui-z-index;
}

.client_unit_stats__icon {
    display: block;
    flex-shrink: 0;
    height: 16px;
    margin-left: 4px;
    width: 16px;

    &:first-child {
        margin-left: 0;
    }
}

.client_unit_stats__text {
    //display: block;
    outline: none;
}
