@import "/www/css/var";
@import "/www/css/service";

.bottom_status_bar {
    align-items: stretch;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    height: $bottom-status-bar-size;
    justify-content: flex-start;
    width: 100%;
    z-index: $game-ui-z-index;
}

.bottom_status_bar__main_block {
    @extend %ui-border-1;

    align-items: center;
    background-color: transparent;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    gap: 0 32px;
    justify-content: flex-start;
    padding-left: 8px;
}
