@import "/www/css/service";

.page_header {
    @extend %ui-border-1;

    align-items: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    image-rendering: pixelated;
    justify-content: space-between;
    padding: 5px 4px 1px;
}

.page_header__text {
    @extend %ui-header-text;

    display: block;
    line-height: 1;
    text-align: right;
}

.page_header__back_button {
    @extend %ui-header-text;

    background-color: transparent;
    border: 0;
    cursor: pointer;
    display: block;
    flex-shrink: 0;
    line-height: 1;
    margin: 0;
    padding: 0;
    text-align: left;
}
