@import '/www/css/service';
@import '/www/css/var';

.unit_store_item {
    box-sizing: border-box;
    display: block;
    max-width: 100%;
    padding: 16px 0;
    width: 440px;
}

.unit_store_item__header {
    @extend %ui-header-text;

    align-items: end;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
    line-height: 1.15;
    margin: 0;
    padding-bottom: 8px;
}

.unit_store_item__main {
    align-items: center;
    display: flex;
    flex-flow: row wrap;
    gap: 8px 8px;
    justify-content: space-between;
}

.unit_store_item__main_data {
    align-items: center;
    display: flex;
    flex-flow: row wrap;
    gap: 8px 8px;
    justify-content: space-between;
}

.unit_store_item__unit_image {
    display: block;
    flex-shrink: 0;
    height: $square-size * 2;
    image-rendering: pixelated;
    width: $square-size * 2;
}

.unit_store_item__unit_stats_info {
    display: block;
}

.unit_store_item__unit_stats_info__line {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.unit_store_item__unit_stats_info__icon {
    display: block;
    flex-shrink: 0;
    height: $square-size;
    image-rendering: pixelated;
    width: $square-size;
}

.unit_store_item__unit_stats_info__text {
    padding-left: 2px;
    padding-right: 6px;
}

.unit_store_item__unit_stats_info__buy_button_wrapper {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.unit_store_item__unit_description {
    color: $main-font-color;
    display: block;
    font-family: $main-font-family-monospace;
    font-size: 14px;
    line-height: 1.4;
    margin: 0;
    padding: 16px 0 0 ;
}
