@import './../../client-game-view-container/client-game-view-container';

.unit_square {
    animation-fill-mode: both;
    height: $square-size;
    image-rendering: pixelated;
    overflow: hidden;
    position: absolute;
    transform: translate3d(0, 0, 0);
    width: $square-size;
}

.unit_image_wrapper {
    animation-direction: normal;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: unit-image-shift;
    animation-timing-function: steps(2);
    display: block;
    height: $square-size;
    position: absolute;
    width: $square-size;
    z-index: 0;
}

@keyframes unit-image-shift {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-400%);
    }
}

.unit_image {
    display: block;
    margin-top: $square-size;

    &:first-child {
        margin-top: 0;
    }
}

.unit_health {
    bottom: 0;
    left: 0;
    position: absolute;
    z-index: 1;
}

.unit_additional_state_wrapper {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 1;
}

.unit_additional_state_image {
    display: block;
}

.unit_level {
    bottom: 0;
    position: absolute;
    right: 0;
    z-index: 1;
}

.unit_change_health {
    align-items: center;
    display: flex;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: $square-size;
    z-index: 1;
}

.unit_level_up {
    display: block;
    height: calc($square-size / 2);
    image-rendering: pixelated;
    left: 0;
    margin: calc($square-size / 2) 0 0 (-$square-size);
    opacity: 0;
    position: absolute;
    top: 0;
    width: $square-size * 3;
    z-index: 1;
}
