@import "/www/css/var";

.bottom_status_bar {
    align-items: center;
    background-color: #7d7e99;
    border-top: 1px solid #ccc;
    bottom: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    height: $bottom-status-bar-size;
    justify-content: center;
    left: 0;
    overflow: auto;
    position: fixed;
    width: 100%;
    z-index: $game-ui-z-index;
}
