@import "/www/css/service";
@import "/www/css/var";

.unit_store {
    background-color: transparent;
    background-image: none;
    left: 0;
    position: fixed;
    top: 0;
    z-index: $unit-store-z-index;
}

.unit_store__unit_list {
    backdrop-filter: blur(1px);
    background-color: rgb(36 42 69 / 90%);
    color: $main-font-color;
}

.unit_store__unit_list__content {
    background-color: transparent;
}

.unit_store__footer {
    background-color:transparent;
    display: block;
    height: $bottom-status-bar-size;
}
