@import './var';

button, a {
    -webkit-tap-highlight-color: transparent;
}

%width-limit,
.width_limit {
    background-color: $main-background-color;
    box-sizing: border-box;
    display: block;
    font-family: $main-font-family-monospace;
    margin: 0 auto;
    max-width: $max-width;
    padding-left: $ui-unit * 3;
    padding-right: $ui-unit * 3;
}

%ellipsis,
.ellipsis {
    display: block;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

