        width: 8px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #c1c1c1;
        border-radius: 2px;
    }
    */
}

.disable_user_event {
    pointer-events: none;
}

.block {
    display: block;
}

.inline {
    display: inline;
}

.double_block_child {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    height: 50%;
    justify-content: center;
    width: 100%;
}

.double_block_child__button_wrapper {
    align-items: stretch;
    display: flex;
    flex-direction: column;
    gap: 8px 0;
}

%ui-header-text {
    color: $main-font-color;
    display: block;
    font-family: $main-font-family-monospace;
    font-size: 22px;
    font-weight: normal;
    letter-spacing: 1.2px;
    line-height: 1.4;
    margin: 0;
    padding: 0;
    text-decoration: none;
    text-shadow: 3px 3px 0 #000c;
}

%ui-border-1 {
    background-color: $dark-blue;
    border-image: url('/www/css/image/border-1.png') 7 / 14px repeat repeat;
    border-style: solid;
    border-width: 12px;
    image-rendering: pixelated;
}

%ui-border-game-map {
    border-image: url('/www/css/image/border-game-map.png') 24 / 24px repeat repeat;
    border-style: solid;
    border-width: 24px;
    image-rendering: pixelated;
}

.flex_row_center {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@media screen and (orientation: landscape) {
    body {
        .double_block_wrapper {
            flex-direction: row;
        }
    }

    .double_block_child {
        height: 100%;
        width: 50%;
    }
}

@media screen and (max-width: $tablet-width) {
    %width-limit,
    .width_limit {
        padding-left: $ui-unit * 2;
        padding-right: $ui-unit * 2;
    }
}



body {
    .bg_color__red {
        background-color: #e01d65;
    }

    .br_color__red__gradient {
        background-image: linear-gradient(0deg, #5F0578, #F49E9C);
    }

    .bg_color__blue {
        background-color: #4195e6;
    }

    .br_color__blue__gradient {
        background-image: linear-gradient(0deg, #0C3570, #96D9F4);
    }

    .bg_color__green {
        background-color: #01ad32;
    }

    .br_color__green__gradient {
        background-image: linear-gradient(0deg, #005552, #ABED5A);
    }

    .bg_color__black {
        background-color: #004173;
    }

    .br_color__black__gradient {
        background-image: linear-gradient(0deg, #001630, #007696);
    }


    .bg_color__gray {
        background-color: #626262;
    }

    .br_color__gray__gradient {
        background-image: linear-gradient(0deg, #303030, #959595);
    }
}

.input_text {
    background-color: #fff;
    border: 0;
    box-sizing: border-box;
    color: #000;
    display: block;
    font-family: $main-font-family-monospace;
    font-size: 18px;
    line-height: 18px;
    margin: 0;
    max-width: 100%;
    padding: 11px 12px 9px;
}

.input__select__label {
    align-items: center;
    display: flex;
    flex-flow: row wrap;
    gap: 8px 16px;
    justify-content: flex-start;
    margin: 0;
    padding: 0;
}

.input__select__text {
    @extend %ui-header-text;

    font-size: 18px;
    margin: 0;
    padding: 0;
}

.input__select {
    background-color: #fff;
    border: 0;
    box-sizing: border-box;
    color: #000;
    display: block;
    font-family: $main-font-family-monospace;
    font-size: 18px;
    line-height: 18px;
    margin: 0;
    padding: 8px 9px 6px;
}
