@import "/www/css/service";

.ui_button {
    @extend %ui-header-text;

    align-items: center;
    background-color: #1a1d29;
    background-image: linear-gradient(0deg, #1a1d29, #333C57);
    border: 0;
    border-radius: 4px;
    box-shadow: 3px 3px 3px 0 #0005;
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
    padding: 7px 16px 4px;
    text-decoration: none;
    transition: opacity 0.3s linear;

    /*
    &:hover {
        background-color: #2e3659;
        background-image: linear-gradient(0deg, #333C57, #1a1d29);
        box-shadow: 1px 1px 0 0 #000;
    }
    */

    &:active {
        background-color: #2e3659;
        background-image: linear-gradient(0deg, #333C57, #1a1d29);
        box-shadow: 1px 1px 0 0 #000;
        //box-shadow: none;
    }

    &.ui_button__active {
        background-color: #2e3659;
        background-image: linear-gradient(0deg, #333C57, #1a1d29);
        box-shadow: none;
        outline: 2px solid #c008;
    }

    &.ui_button__disabled {
        opacity: 0.5;
        pointer-events: none;
    }

    .ui_button__icon {
        display: block;
        height: 36px;
        image-rendering: pixelated;
        width: 36px;
    }

    .ui_button__text {
        @extend %ui-header-text;

        &.ui_button__text__align_center {
            display: block;
            flex-grow: 1;
            text-align: center;
        }
    }

    &.ui_button__with_image {
        padding-left: 7px;
        padding-right: 10px;
    }

    &.ui_button__squared {
        flex-direction: column;
        gap: 0;
        padding: 7px 9px 4px;

        .ui_button__icon {
            height: 24px;
            width: auto;
        }

        .ui_button__text {
            font-size: 12px;
        }

        &.ui_button__squared__image_only {
            padding: 7px;
        }
    }
}
