@import './../client-game-view-container/client-game-view-container';

.click_hunter_layer {
    align-items: flex-start;
    display: flex;
    flex-flow: row wrap;
    height: 100%;
    justify-content: flex-start;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.click_hunter_active_point {
    height: calc($square-size * 2);
    image-rendering: pixelated;
    left: calc($square-size / -2);
    overflow: hidden;
    position: absolute;
    top: calc($square-size / -2);
    width: calc($square-size * 2);
    z-index: -1;
}

@keyframes active-point-cursor-shift {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-100%);
    }
}

.click_hunter_active_point_animation_wrapper {
    animation-direction: normal;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: active-point-cursor-shift;
    animation-timing-function: steps(2);
    height: calc($square-size * 4);
    width: calc($square-size * 2);
}

.click_hunter_active_point_animation_image {
    display: block;
    height: calc($square-size * 2);
    width: calc($square-size * 2);
}
