.landscape_layer {
    align-items: flex-start;
    display: flex;
    flex-flow: row wrap;
    height: 100%;
    justify-content: flex-start;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.landscape_layer_image {
    display: block;
    height: 100%;
    image-rendering: pixelated;
    width: 100%;
}
