@import "/www/css/service";

.client_load_game_popup__saved_game_list {
    @extend %scrollable;

    align-items: stretch;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    padding: 8px 4px 12px;
}

.client_load_game_popup__no_saved_game_text {
    @extend %ui-header-text;

    padding: 12px 0;
    text-align: center;
}

.client_load_game_popup__saved_game_item {
    align-items: stretch;
    border-bottom: 1px solid #fff;
    display: flex;
    flex-direction: column;
    gap: 8px 0;
    justify-content: stretch;
    padding: 8px 0;
}

.client_load_game_popup__saved_game_item__name {
    @extend %ui-header-text;

    font-size: 16px;
}

.client_load_game_popup__saved_game_item__button_list {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 16px;
    justify-content: space-evenly;
}
