@import './../client-game-view-container/client-game-view-container';

.building_layer {
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

%building-item,
.building_item {
    display: block;
    height: $square-size;
    image-rendering: pixelated;
    position: absolute;
    width: $square-size;
}

.building_item__castle {
    @extend %building-item;

    height: $square-size * 2;
}
