@import "/www/css/service";

.client_local_game_setup__content {
    align-items: stretch;
    display: flex;
    flex-direction: column;
    gap: 16px 0;
    justify-content: flex-start;
    margin: 0;
    padding: 0;
}

.client_local_game_setup__map_name {
    @extend %ui-header-text;

    display: block;
    font-size: 26px;
    margin: 0;
    padding: 0 0 8px;
    text-align: center;
}

.client_local_game_setup__item {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: space-between;
}

.client_local_game_setup__item_label {
    @extend %ui-header-text;

    margin: 0;
    padding: 0;
}

.client_local_game_setup__item_input_wrapper {
    display: block;
}

.client_local_game_setup__footer {
    padding: 8px 0;
}
