@import "/www/css/service";

.client_local_game_setup_player {
    @extend %ui-header-text;

    align-items: center;
    display: flex;
    flex-flow: row wrap;
    gap: 8px;
    justify-content: space-between;
}

.client_local_game_setup_player__color_point {
    display: block;
    height: 24px;
    image-rendering: pixelated;
    width: 24px;
}

.client_local_game_setup_player__color_name {
    display: block;
    margin: 0;
    padding: 2px 0 0;
}

.client_local_game_setup_player__left,
.client_local_game_setup_player__right {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
}

.client_local_game_setup_player__right {
    justify-content: flex-end;
}
